/* Global Styles */
body, html {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  background-color: #f4f4f4;
  color: #333;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: inherit;
}

/* Navbar */
.navbar {
  background-color: #2c3e50;
  color: #ecf0f1;
  padding: 10px;
  text-align: center;
  font-size: 1.2rem;
}

/* Main Content Layout */
.main-content {
  display: flex;
  flex-direction: column;
}

/* SideNav Container */
.sidenav {
  width: 100%;
  height: auto;
  background-color: #2c3e50;
  animation: slideIn 0.5s ease-in-out;
  display: none; /* Hidden by default on mobile */
}

@keyframes slideIn {
  from {
    transform: translateX(-100%);
  }
  to {
    transform: translateX(0);
  }
}

.sidenav-header {
  text-align: center;
  padding: 20px;
  color: #ecf0f1;
}

.sidenav ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.sidenav ul li {
  margin: 15px 0;
  padding: 10px;
  border-radius: 5px;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.sidenav ul li:hover {
  background-color: #34495e;
  transform: translateX(10px);
}

.sidenav ul li a {
  color: #ecf0f1;
  text-decoration: none;
  font-size: 1.1rem;
  display: flex;
  align-items: center;
  gap: 10px;
}

.sidenav ul li a:hover {
  color: #46dc46;
}

/* Logout Button */
.sidenav ul li button {
  background: none;
  border: none;
  color: #ecf0f1;
  font-size: 1.1rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 0;
}

.sidenav ul li button:hover {
  color: #46dc46;
}

/* Dashboard Styles */
.dashboard {
  padding: 20px;
  text-align: center;
}

.dashboard h1 {
  margin-bottom: 20px;
  color: #333;
}

.dashboard-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
  padding: 20px;
}

.class-box {
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset, rgb(119, 158, 209) 0px 18px 36px -18px inset;
  text-align: center;
  cursor: pointer;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.class-box:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.304);
}

.class-box span {
  font-size: 1.5rem;
  color: #333;
}

.class-box p {
  font-size: 1rem;
  color: #666;
}

/* Reports Grid in Dashboard */
.reports-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
  padding: 20px;
}

.report-box {
  background-color: #fff;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
  cursor: pointer;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.report-box:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.43);
}

.report-box span {
  font-size: 1.5rem;
  color: #fff;
}

.attendance-report {
  background-color: #3498db;
}

.assignments-report {
  background-color: #e67e22;
}

.grades-report {
  background-color: #2ecc71;
}

/* Report Page Styles */
.report-page {
  padding: 20px;
  text-align: center;
  overflow: visible;
}

.back-button {
  background-color: #333;
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  margin-bottom: 20px;
  font-size: 1rem;
  position: absolute;
  left: 20px;
  top: 20px;
}

.back-button:hover {
  background-color: #555;
}

.report-content {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-top: 60px;
  overflow: visible;
}

.graph-container {
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  height: 400px;
  overflow: visible;
}

.graph-container p {
  font-size: 0.6rem;
  color: #555;
}

.summary-container {
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: left;
  margin-top: 20px;
  overflow: visible;
}

.summary-container h2 {
  margin-bottom: 10px;
  color: #333;
}

.summary-container ul {
  list-style: none;
  padding: 0;
}

.summary-container ul li {
  margin: 10px 0;
  font-size: 1rem;
  color: #555;
}

/* Class Details Page Styles */
.class-details-page {
  display: flex;
  flex-direction: column;
}

.content {
  flex: 1;
  padding: 20px;
}

.content h1 {
  margin-bottom: 20px;
  color: #333;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

th, td {
  padding: 12px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}

th {
  background-color: #333;
  color: white;
}

tr:hover {
  background-color: #f5f5f5;
}

/* Login Page Styles */
.login-page {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background: linear-gradient(135deg, #2c3e50, #81d9e8);
  animation: gradientAnimation 10s ease infinite;
}

@keyframes gradientAnimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.login-container {
  background: rgba(255, 255, 255, 0.9);
  padding: 20px;
  border-radius: 15px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
  text-align: center;
  animation: fadeIn 1.5s ease-in-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.login-title {
  font-size: 2rem;
  color: #333;
  margin-bottom: 20px;
  animation: slideIn 1s ease-in-out;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateX(-50px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.login-container p {
  font-size: 0.9rem;
  color: #e47f7fc0;
  margin-bottom: 20px;
}

.login-form {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.form-group {
  text-align: left;
}

.form-label {
  font-size: 1rem;
  color: #555;
  margin-bottom: 5px;
  display: block;
}

.form-input {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 8px;
  font-size: 1rem;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

.form-input:focus {
  border-color: #f47979;
  box-shadow: 0 0 10px rgba(244, 121, 121, 0.3);
  outline: none;
}

.error-message {
  color: #ff4d4d;
  font-size: 0.9rem;
  margin-top: 10px;
  animation: shake 0.5s ease-in-out;
}

@keyframes shake {
  0% {
    transform: translateX(0);
  }
  25% {
    transform: translateX(-10px);
  }
  50% {
    transform: translateX(10px);
  }
  75% {
    transform: translateX(-10px);
  }
  100% {
    transform: translateX(0);
  }
}

.login-button {
  background-color: #f47979;
  color: white;
  padding: 10px;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.login-button:hover {
  background-color: #ff6f61;
  transform: scale(1.05);
}

.login-button:active {
  transform: scale(0.95);
}

/* Assignment, Attendance, and Marks Page Styles */
.assignment-page,
.attendance-page,
.marks-page {
  display: flex;
  flex-direction: column;
  height: 100vh;
  overflow-y: auto;
  background-color: #f4f4f4;
  padding: 10px;
}

/* Content Styles */
.content {
  flex: 1;
  padding: 10px;
}

.content h1 {
  margin-bottom: 20px;
  color: #333;
}

/* Container Styles */
.assignment-container,
.attendance-container,
.marks-grades-container {
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-height: 80vh;
}

/* Table Styles */
table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
}

th, td {
  padding: 12px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}

th {
  background-color: #333;
  color: white;
  position: sticky;
  top: 0;
}

tr:hover {
  background-color: #f5f5f5;
}

/* Responsive Styles */
@media (min-width: 768px) {
  .main-content {
    flex-direction: row;
  }

  .sidenav {
    width: 200px;
    height: 100vh;
    display: block;
  }

  .content {
    flex: 1;
    padding: 20px;
  }
}

@media (max-width: 768px) {
  .dashboard-grid,
  .reports-grid {
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  }

  .class-box,
  .report-box {
    padding: 15px;
  }

  .graph-container {
    height: 300px;
  }

  .login-container {
    padding: 20px;
  }

  .login-title {
    font-size: 1.8rem;
  }

  .form-input {
    padding: 8px;
  }

  .login-button {
    padding: 8px;
  }
}

@media (max-width: 480px) {
  .dashboard-grid,
  .reports-grid {
    grid-template-columns: 1fr;
  }

  .class-box,
  .report-box {
    padding: 10px;
  }

  .graph-container {
    height: 250px;
  }

  .login-container {
    padding: 15px;
  }

  .login-title {
    font-size: 1.5rem;
  }

  .form-input {
    padding: 6px;
  }

  .login-button {
    padding: 6px;
  }
}